//StyledJsx.jsx :    Next.js     , scss     !!

export const StyledJsx=()=>{
    return(
        <>
            <div className="container">
                <p className="title">StyledJsx .</p>
                <button className="button"></button>
            </div>

            <style jsx>{`
                .container{
                    border:1px solid gray;
                    padding:8px;
                    margin:8px;
                    display: flex;
                    justify-content: space-around;
                    align-items: center;
                    border-radius: 20px;;
                }
                .title{
                    color:gray;
                    font-size:1.5em;
                }
                .button{
                    background-color: lightgray;
                    border:none;
                    padding:10px;
                    border-radius: 8px;
                    &:hover{
                        background-color: darkgray;
                        cursor: pointer;
                        color:white;
                    }
                }       
            
            `}</style>
        </>
    );
}